<script>
import io from 'socket.io-client';
const socket = io('http://127.0.0.1:8888');


import light from '../components/breathLight.vue'
import add1 from '../components/add1.vue'


export default {
  name: 'HomePage',
  data() {
    return {
      pageName: 'home',
      lightVal: false,
      showBlack: false,
      // showAdd: false,
      showAdd: null,
      bottom3window: false,
      bottom3Light: false,
      showAddBg: false,
      bottom3Num: 1,
      blackPage: null,
      showMenu: false,
      showMode: false,
      showModeMenu: false,
      modeBtn1: true,
      modeBtn2: true,
      modeBtn3: true,
      inner2: false,
      inner3: false




    };
  },
  components: {
    light, add1
  },
  mounted() {
    window.aaa = this;

    this.$nextTick(() => {
      this.$refs[`homeVideo`].play();
      socket.emit('controlLight', 'A0');
    })

    socket.on('comData', (data) => {

      console.log(data)
    })

    socket.on('videoControl', (data) => {

      if (data.order == 'add1') {
        return
      }

      if (data.order == 'reset') {
        return
      }

      if (data.order == 'bottom3') {
        this.bottom3Num = data.msg;
        return
      }

      if (data == 'closeAdd') {
        this.showAdd = false;
        return
      }

      if (data.order == 'showAdd') {
        this.showAddBg = true;
        this.showAdd = data.msg;
        return
      }

      if (data == 'changeLang') {
        this.changeLang();
        return
      }


      this.pageName = data


    })



  },
  computed: {
    PageLang() {
      return this.$route.query.lang
    },
  },
  watch: {
    inner3(newV, oldV) {
      if (newV) {
        this.$refs.bottom3VideoInner3.play();
      } else {
        this.$refs.bottom3VideoInner3.pause();
        this.$refs.bottom3VideoInner3.currentTime = 0;
      }
    },
    inner2(newV, oldV) {
      if (newV) {
        this.$refs.bottom3VideoInner2.play();
      } else {
        this.$refs.bottom3VideoInner2.pause();
        this.$refs.bottom3VideoInner2.currentTime = 0;
      }
    },
    bottom3Num(newV, oldV) {
      // 内页视频组初始化
      let newVideo = this.$refs[`bottom3Video${newV}`];
      let oldVideo = this.$refs[`bottom3Video${oldV}`];
      if (oldVideo) {
        oldVideo.pause();
        oldVideo.currentTime = 0;
      }
      if (this.showAdd == 'add3') {
        if (newVideo) newVideo.play();
      }



    },
    showBlack(newV, oldV) {
      if (newV) {
        this.$refs.blackVideo.play();
      } else {
        this.$refs.blackVideo.pause();
      }

    },
    showAdd(newV, oldV) {
      console.log(newV);

      if (newV == 'add2') {
        this.showMode = true;
      }

      if (newV == 'add3') {
        this.$refs.bottom3Video1.play();
      }

      if (oldV == 'add2') {

        this.modeBtn1 = true;
        this.modeBtn2 = true;
        this.modeBtn3 = true;

        try {
          this.$refs.modeRef.contentWindow.postMessage('dispose');
        } catch (error) {
          console.log(error);
        }


        setTimeout(() => {
          this.showMode = false;
        }, 1000)

      }


      if (oldV == 'add3') {
        this.bottom3Num = 1;
        this.$refs.bottom3Video1.pause();
        this.$refs.bottom3Video1.currentTime = 0;

      }

    },
    lightVal(newV) {
      if (newV == true) socket.emit('controlLight', 'A2');
      if (newV == false) socket.emit('controlLight', 'A0');

    },
    pageName(newV, oldV) {

      socket.emit('controlPage', newV);


      // 内页视频组初始化
      let newVideo = this.$refs[`${newV}Video`];
      let oldVideo = this.$refs[`${oldV}Video`];
      if (oldVideo) {
        oldVideo.pause();
        oldVideo.currentTime = 0;
      }
      if (newVideo) newVideo.play();



    },
    PageLang: {
      // immediate:true,
      handler(newV) {
        this.$i18n.locale = newV;

        this.$nextTick(() => {
          this.$refs[`homeVideo`].play();
        })


      }
    },

  },
  methods: {
    changeMode(msg) {

      this[`modeBtn${msg}`] = !this[`modeBtn${msg}`];

      this.$refs.modeRef.contentWindow.postMessage(msg);

    },
    changeContent(msg) {

      this.showMenu = false;
      this.showAddBg = false;
      this.showBlack = false;
      this.blackPage = null;

      if (msg == 'add') {
        this.showAddBg = true;
        this.pageName = 'home'
      }

      if (msg == 'black') {
        this.showBlack = true;
        this.pageName = 'home'
      }

    },
    controlSky3(msg) {
      let obj = {
        order: 'bottom3',
        msg
      }

      if (msg == 'closeLight') {
        socket.emit('controlPage', obj);
        this.bottom3window = false;
        return
      }

      if (msg == 'light') {
        socket.emit('controlPage', obj);
        this.bottom3window = true;
        return
      }

      this.bottom3Num = msg
      socket.emit('controlPage', obj);
      this.bottom3Num = msg


    },
    controlSky2(msg) {
      let obj = {
        order: 'bottom2',
        msg
      }


      socket.emit('controlPage', obj);

    },
    closeAdd() {
      this.showAdd = false
      socket.emit('controlPage', 'closeAdd');

    },
    toShowAdd(msg) {

      this.showAddBg = true;


      this.showAdd = msg;
      let obj = {
        order: 'showAdd',
        msg
      }
      socket.emit('controlPage', obj);

    },
    Lang() {
      this.changeLang();
      socket.emit('controlPage', 'changeLang');
    },
    //中英文切换
    changeLang() {

      if (this.PageLang == 'en-US') {
        this.$router.push({
          query: {
            lang: 'cn-ZH'
          }
        });
      } else {
        this.$router.push({
          query: {
            lang: 'en-US'
          }
        });
      }
    },


  },
};
</script>

